<template>
  <div v-show="show" class="w-100 h-100 position-fixed" style="z-index:9999;margin-top:0px;background-color:rgba(240,240,240,0.5);">
    <button type="button" class="close p-2" aria-label="Close" @click="closeMyPage()">
      <span aria-hidden="true">&times;</span>
    </button>
    <div class="pt-2">
      <form>
        <div class="input-group w-75 m-auto">
          <input type="text" class="form-control" v-model="searchString">
          <div class="input-group-append">
            <button type="button" class="btn btn-primary" @click="search()">{{$t("search")}}</button>
          </div>
        </div>
      </form>
      <div class="list-group list-group-flush m-4 pre-scrollable">
        <a href="#" class="list-group-item list-group-item-action" v-for="(item, index) in searchResult" :key="index">{{item.name}}</a>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      show: false,
      articles: [],
      searchString: '',
      searchResult: []
    }
  },
  created () {
    this.articles = JSON.parse(localStorage.getItem('articles'))
  },
  methods: {
    display () {
      this.show = true
    },
    closeMyPage () {
      this.show = false
    },
    search () {
      var vueObj = this
      vueObj.articles.forEach(function (item, index) {
        if (
          item.name.indexOf(vueObj.searchString) ||
          item.tags.forEach(function (tag) {
            if (vueObj.searchString.indexOf(tag)) {
              return true
            }
          })
        ) {
          console.log(vueObj.searchString)
          vueObj.searchResult.push(item)
        }
      })
    }
  }
}
</script>
<style>
</style>



